
#app {
   height: 100%;
   background: #0d1015;
   color: #a2a5ab;
   min-width: 320px;
   max-width: 450px;
   width: 100%;
   position: relative;
}
.float-left {
   float: left;
}
#top {
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: justify;
       justify-content: space-between;
   font-size: 22px;
   padding: 10px;
   background: #171a23;
}
#top img {
   background: white;
   height: 20px;
   margin-top: 5px;
}
#top img.email{
   margin-left: 9px;
}
#top img.search{
   margin-right: 9px;
}
#connection_error {
   background: #4f181e;
   color: gray;
   font-size: 14px;
   padding: 8px 16px;
}
#connection_error a.check {
   text-decoration: none;
   float: right;
   color: #425171;
}
#connection_error .info {
   display: -ms-flexbox;
   display: flex;
   -ms-flex-align: center;
       align-items: center;
}
#connection_error img {
   width: 18px;
   margin-right: 10px;
}
#nav {
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: distribute;
       justify-content: space-around;
   -ms-flex-align: center;
       align-items: center;
   margin: 0;
   padding: 12px 0 0;
   background: #171a23;
}
#nav li {
   list-style: none;
   padding: 0;
   margin: 0;
   font-size: 17px;
   padding-bottom: 8px;
}
#nav li.active {
   color: #2474d6;
   border-bottom: 2px solid #2474d6;
}
#nav img {
   height: 17px;
}
.header button {
   background: #1a2333;
   border: none;
   color: #266bb7;
   padding: 4px 6px;
   font-size: 14px;
   border-radius: 2px;
}
table {
   background: #171a23;
   margin-top: 9px;
   width: 100%;
   font-size: 14px;
   border-collapse: collapse;
}
th,td{
   text-align: right;
   height: 35px;
   box-sizing: border-box;
}
th {
   padding: 12px 0;
   border-bottom: 1px solid #0b0e16;
}
th .sort{
   height: 15px;
}
td {
   font-size: 17px;
   padding-bottom: 12px;
   color: #c1c5c4;
   border-bottom: 1px solid #0c0f17;
   padding: 7px 0 7px 0;
   line-height: 1.7;
}
td:first-child,th:first-child {
   text-align: left;
   padding-left: 18px;
}
td:last-child,th:last-child {
   padding-right: 18px;
}
td span.subtitle {
   display: block;
   font-size: 12px;
   color: rgba(128, 128, 128, 0.61);
}
td button{
   font-size: 16px;
   width: 95px;
   padding: 6px;
   color: white;
   text-align: right;
   border: none;
   border-radius: 3px;
}
.down {
   color: green;
}
.down-button {
   background: green;
}
.up {
   color: red;
}
.up-button {
   background: red;
}
.blue-mark, .pink-mark {
   border-radius: 2px;
   padding: 0 3px;
}
.blue-mark {
   background: #1d76d5;
   color: black;
}
.pink-mark {
   background: #a338cb;
   color: black;
}
.pre {
   background: #80808052;
   font-size: 12px;
   color: rgba(255, 255, 255, 0.42);
   padding: 2px 4px;
   display: inline-block;
   line-height: 1;
   margin-top: 4px;
   border-radius: 2px;
}
#bottom {
   position: absolute;
   bottom: 0;
   right: 0;
   padding: 9px 14px;
   font-size: 15px;
   display: -ms-flexbox;
   display: flex;
   width: 100%;
   -ms-flex-pack: justify;
       justify-content: space-between;
   background: #171a23;
   box-sizing: border-box;
}
.clickable {
   cursor: pointer;
}
.clickable div{
   display: -ms-flexbox;
   display: flex;
   -ms-flex-align: center;
       align-items: center;
   -ms-flex-pack: end;
       justify-content: flex-end;
}
.clickable div img{
   height: 15px;
   margin-left: 3px;
}
.clickable div img.mode{
   height: 18px;
   margin-left: 8px;
}
.white {
   color: white
}
#loading{
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: center;
       justify-content: center;
   -ms-flex-align: center;
       align-items: center;
   width: 100%;
   height: 300px;
}




